	module.exports = "<div style=\"width: 100%;\">\n  <section class=\"bannerTitle\" id=\"home\">\n    <h1 class=\"titlemax\">CAMPUS<br/>AMBASSADOR<br/>PORTAL</h1>\n    <p class=\"tagline\">SHAASTRA 2019</p>\n    <a ui-sref=\"signup\" ng-hide=\"$ctrl.isLoggedIn()\" ui-sref-active=\"active\" style=\"text-decoration: none\"><div class=\"button_black\">register</div></a>\n  </section>\n\n  <!-- About: Why -->\n  <section class=\"about\" id=\"one\">\n    <h2 class=\"white\">WHY BECOME A CA?</h2>\n    <p class=\"text_column\">The Campus Ambassador Program is a platform for you to improve your communication, marketing and social skills. It offers a whole range of activities which give you a hands-on experience in digital marketing, publicity and event management.</p>\n    <p class=\"text_column\">CAs get a unique opportunity to represent Shaastra, IIT Madras for their institute. They also get to work with the team behind the world's first ISO certified and one of the largest technical festivals in India.</p>\n  </section>\n\n  <!-- About: How -->\n  <section class=\"banner\" id=\"two\">\n    <h2 class=\"black\">HOW IT WORKS</h2>\n    <p class=\"parallax_description\">CAs are assigned a range of tasks from time to time. These tasks range from carrying out publicity to organizing workshops. Every task carries a fixed number of points, which are awarded upon completion of the task. For example, one post shared on Facebook can give the CA one point. Tasks which require more efforts like conducting on-ground publicity shall carry higher points.</p>\n    <p class=\"parallax_description\">CAs are required to complete a fixed minimum number of points to receive the marketing internship certificate. Those who grab extra points will be eligible to receive additional incentives such as goodies.</p>\n  </section>\n\n  <!-- Parallax: What -->\n  <section class=\"banner\" id=\"four\">\n    <h2 class=\"black\">YOUR RESPONSIBILITIES</h2>\n    <p class=\"parallax_description\">You will be the representative of Shaastra in your region. You will be responsible for marketing Shaastra and its events in your city to gain maximum participation. You will also get an opportunity to completely organize Shaastra's events in your city.</p>\n  </section>\n\n  <!-- About: Contact -->\n\n\n  <section class=\"about\" id=\"five\" style=\"width: 100%;\">\n      <h2 class=\"white\">TERMS AND CONDITIONS FOR CA PROGRAM</h2>\n        <ul class=\"white\">\n          <li>The CA will be a part of the organizing team of Shaastra 2018 and he/she must ensure that his/her college has no objection to this.</li>\n          <li>Shaastra will not be responsible for any issues arising due to objection of college authorities in the later stage.</li>\n          <li>The marketing internship certificate will only be provided subject to completion of fixed minimum points.</li>\n          <li>Shaastra reserves the right to change the points awarded for every task and minimum points required for the certificate as well as bonus incentives and goodies.</li>\n          <li>Tasks allotted to CAs will include, but not be limited to:</li>\n            <ol class=\"white\">\n              <li>Putting up posters</li>\n              <li>Sharing posts put up by the Shaastra Facebook page</li>\n              <li>Sending mails for publicity</li>\n              <li>Getting MoUs signed</li>\n              <li>Taking care of organizational aspects of events including facilities and requirements</li>\n              <li>Visiting colleges or other public venues for carrying out on-ground publicity</li>\n              <li>Getting participants through referrals</li>\n            </ol>\n          <li>All official communication will be done via the CA portal and email. It is the responsibility of the CA to check his/her email and the CA portal regularly and acknowledge tasks assigned to him/her.</li>\n          <li>Should there be any discrepancies or issues, the decision taken by Shaastra shall be final and binding.</li>\n        </ul>\n  </section>\n\n  <section class=\"banner\" id=\"six\" style=\"width: 100%; padding-bottom: 10vh;\">\n    <h2 class=\"black\">CONTACT US</h2>\n    <p class=\"parallax_description\">\n        E-MAIL: <a href=\"mailto:studentrelations@shaastra.org\" target=\"blank\">studentrelations@shaastra.org</a>\n        <br/><br/>\n        Abijith PY - +91 94 96 670178<br/>\n        Parth Deshpande - +91 99 40 113503<br/>\n        Sai Shandilia - +91 80 56 157508<br/>\n        Kapeleshh Ks - +91 94 86 720117<br/>\n    </p>\n  </section>\n</div>\n"

